

 .swiper {

    .swiper-button-prev,
    .swiper-button-next {

        border-radius: 50%;
        width: 40px;
        height: 40px;
    }

    .swiper-button-prev {
        left: 5%;
    }

    .swiper-button-next {
        right: 5%;
    }
}


.tipeItem {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 16px;
    max-height: 560px;

    .sliderButtons {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        column-gap: 14px;

        .sliderButton {
            border: 0;
            padding: 0;
            width: 40px;
            height: 40px;
            background-image: url(../assets/arr-prev.svg);
            cursor: pointer;
        }

        .upButton {
            transform: rotate(90deg);
        }

        .downDutton {
            transform: rotate(-90deg);
        }
    }
}

.sliderBlockItem {
    display: flex;

    width: 730px;
    height: 560px;
    height: max-content;
}

.sliderBlockTipe {
    display: flex;
    width: 94px;
    overflow: visible;
    margin: 0;
    max-height: 560px;

    .swiper-slide {
        opacity: 0.6;
        max-height: 114px;
        cursor: pointer;
    }

    .swiper-wrapper {
        flex-direction: column;
        row-gap: 8px;
        width: 94px; 
    }

    img {
        width: 94px!important;
        height: 114px;
     
    }

    .swiper-button-next,
    .swiper-button-prev {
        position: unset;
        z-index: 10;
    }

    .swiper-slide-thumb-active {
        opacity: 1;
    }
}


@media all and (max-width: 630px) {
   .sliderBlockTipe {
       width: 100%;
       .swiper-wrapper {
         flex-direction: row;
         justify-content: flex-start;
       }
     }
     .tipeItem {
       width: 100%;
       flex-wrap: wrap;
     }
    
    .sliderBlockItem {    
        width: 100%;
        height: max-content;  
        .sliderImg{
            width: 100%;
        }  
    }
}